export default {
  name: 'giftCardItem',
  props: {
    price: {
      type: [String, Number],
      default: 0,
    }, // 金额
    date: {
      type: [String, Number],
      default: '',
    }, // 时间
    number: {
      type: [String, Number],
      default: '',
    }, // 卡号
    image: {
      type: [String],
      default: require('@/static/images/wallet-recharge-card.png'),
    }, // 卡面
    showSendButton: {
      type: [Boolean],
      default: false,
    }, // 是否可赠送
  },
  methods: {
    handleClick(type) {
      switch (type) {
        case 'send':
          this.$emit('send')
          break;
        default:
        case 'use':
          this.$emit('use')
          break;
      }
    },
  },
}
